"use client";

import { useState, useEffect } from 'react';
import { SolarOracleOnboarding } from '../components/SolarOracleOnboarding';

interface OnboardingData {
  experienceLevel: string;
  lifeChallenges: string;
  approachPreference: string;
  transformationGoals: string;
  integrationStyle: string;
  learningPreference: string;
  supportNeeds: string;
  investmentLevel: string;
  readinessLevel: string;
}

export default function DemoOnboardingPage() {
  const [showOnboarding, setShowOnboarding] = useState(true);
  const [onboardingData, setOnboardingData] = useState<OnboardingData | null>(null);

  const handleOnboardingComplete = (data: OnboardingData) => {
    setOnboardingData(data);
    setShowOnboarding(false);
  };

  const handleSkipOnboarding = () => {
    setShowOnboarding(false);
  };

  const resetDemo = () => {
    setShowOnboarding(true);
    setOnboardingData(null);
  };

  if (showOnboarding) {
    return (
      <SolarOracleOnboarding
        onComplete={handleOnboardingComplete}
        onSkip={handleSkipOnboarding}
      />
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-orange-900 via-amber-900 to-yellow-900 p-8">
      <div className="max-w-4xl mx-auto">
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-white mb-4">Onboarding Complete!</h1>
          <button
            onClick={resetDemo}
            className="bg-gradient-to-r from-yellow-500 to-orange-500 text-white px-6 py-3 rounded-lg font-semibold hover:from-yellow-400 hover:to-orange-400 transition-all"
          >
            Reset Demo
          </button>
        </div>

        {onboardingData && (
          <div className="bg-black/40 backdrop-blur-xl border border-yellow-400/30 rounded-3xl p-8">
            <h2 className="text-2xl font-bold text-white mb-6">Captured Onboarding Data:</h2>
            <div className="space-y-4">
              {Object.entries(onboardingData).map(([key, value]) => (
                <div key={key} className="border-b border-white/20 pb-3">
                  <h3 className="text-yellow-300 font-semibold capitalize mb-1">
                    {key.replace(/([A-Z])/g, ' $1').trim()}
                  </h3>
                  <p className="text-white text-sm">{value}</p>
                </div>
              ))}
            </div>
          </div>
        )}
      </div>
    </div>
  );
}